import React,{Component} from 'react';

class ErrorBoundary extends Component{
	constructor(props){
		super(props);
		this.state={
			error:false
		}
	}
	componentDidCatch()
	{
		this.setState({error:true});
	}
	render(){
		if(this.state.error)
			return(<h1>Oops Something went Wrong!!We are Sorry</h1>);
		else
		return(this.props.children);
	}
}
export default ErrorBoundary;