import React from 'react';
import 'tachyons';


const Card=({id,name,email})=>{

		return(

				<div className="bg-light-green br3 pa3 ma2 dib grow bw2 shadow-2 tc">
				<img src={`https://robohash.org/${name}?200x200"`} alt="robot"/>
				<h2>{name}</h2>
				<p>{email}</p>
				</div>
			);
	
}
export default Card;